@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;600;700;800&display=swap);
:root {
  --primary-color: #D7FD35;
  --dark-bg: #323232;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Montserrat', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  font-family: 'Montserrat', sans-serif;
}

.text-bold {
  font-weight: 800;
}

.all-titles {
  color: var(--primary-color);
  font-weight: 800;
  font-size: 60px;
}

.all-text {
  color: white;
  font-size: 18px;
}
.btn-style {
  background-color: var(--primary-color) !important;
  border: black;
  color: black;
  margin-right: 40px;
  font-weight: bold !important;
  width: 306px;
  height: 53px;
  font-size: 24px !important;
}

.btn-style:hover {
  background-color: rgba(215, 253, 53, 0.7) !important;
}

.home-page {
  background-image: url('https://res.cloudinary.com/dxatyucj2/image/upload/v1592166596/bg-bw_hc1uhl.jpg');
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  height: 92vh;
}

.home-info {
  width: 45%;
  margin-left: 76px;
}

.home-title {
  color: white;
  font-size: 96px;
  text-align: left;
}

.home-text {
  text-align: left;
  font-size: 22px;
  color: var(--primary-color);
  margin-top: 20px;
}

.home-buttons {
  margin: 80px auto 0;
}
.contact-page {
  background-color: var(--dark-bg);
  /* height: 100vh; */
}

.contact-info {
  margin-left: 70px;
  padding-top: 30px;
}

.field-name {
  text-align: left !important;
}

.telefone {
  font-size: 40px;
  font-weight: bold;
  text-align: center;
  margin-bottom: 10px;
}

.btn-search {
  width: 150px !important;
  font-size: 18px !important;
  height: 40px;
}
ul {
  list-style-type: circle;
}

.about-page {
  height: 91vh;
  width: 100vw;
}

.about-title {
  background-color: var(--dark-bg );
  width: 25%;
  margin-bottom: 0;
}

.about-text {
  background-color: var(--primary-color );
  font-size: 18px;
  flex: 1 1;
}

.about-txt-format {
  width: 80%;
  margin: 0 auto;
  text-align: left;
}
.login-page {
  background-color: var(--dark-bg);
  height: 91vh;
}

.login-bg {
  background-color: var(--primary-color);
  width: 50%;
  margin: 0 auto;
  height: 91vh;
}

.login-title {
  font-size: 76px;
  font-weight: 700;
}

.login-text {
  font-size: 18px;
  width: 50%;
  margin: 0 auto;
}

.form-fields {
  font-size: 20px;
}

.input-width {
  width: 338px;
}

.login-button {
  width: 338px;
  height: 43px;
  background-color: var(--dark-bg);
  color: white;
  font-weight: 700;
}

.link-style {
  text-decoration: underline;
  color: #323232;
}

.comment {
  font-size: 10px;
  font-style: italic;
}
.profile-page {
  width: 100vw;
  /* height: 100vh; */
}

.profile-header {
  background-color: var(--primary-color);
  width: 30%;
}

.profile-title {
  color: var(--dark-bg);
  font-weight: 700;
  font-size: 60px;
  width: 200px;
  text-align: left;
}

.profile-infos {
  background-color: #2e2e2e;
  flex: 1 1;
  padding: 40px 80px;
}

.white-text {
  color: white;
}

.profile-img {
  width: 250px;
}

.sml-txt {
  font-size: 15px;
  text-align: center;
}

.smlr-txt {
  font-size: 12px;
}

.rgl-txt {
  font-size: 18px;
  text-align: left;
}

.lg-txt {
  font-size: 22px;
  text-align: left;
}

.personal-info p {
  text-align: left;
}

.profile-card {
  width: 350px;
}

.txt-width {
  width: 300px;
}

.form-text {
  margin: 0;
}

.master-form {
  background-color: var(--dark-bg);
  width: 100vw;
}

.btn-pagination {
  background-color: var(--primary-color) !important;
  border-radius: 25px !important;
  font-weight: 600 !important;
  margin-right: 20px;
  margin-top: 20px;
}

.form-steps {
  background-color: white;
  padding: 40px 0;
}

.all-sections {
  width: 100%;
  padding: 10px 80px;
}

.green-bar {
  background-color: var(--primary-color);
  height: 2px;
}

.form-field {
  font-size: 16px;
  text-align: left;
}

.form-texts {
  margin: 10px 60px;
}

.lg-width {
  width: 200px;
}

.section-3 {
  padding: 0px 80px !important;
}
nav {
  height: 60px;
}

nav ul {
  width: 100%;
  margin: 0 60px;
}

.bg-navbar {
  background-color: var(--dark-bg) !important;
}

.navbar-items {
  display: flex;
  justify-content: end;
}
.quali-page {
  background-color: var(--dark-bg);
  padding-top: 50px;
  padding-bottom: 50px;
}

.green-ball {
  border-radius: 50%;
  background-color: var(--primary-color);
  height: 100px;
  width: 100px;
}

.quali-text {
  color: white;
}

.quali-title {
  font-size: 26px;
}

.quali-course {
  font-size: 18px;
}

.quali-link {
  font-size: 14px;
}

.quali-topics {
  width: 35vw;
}

.title-size {
  width: 200px;
  padding-left: 85px;
}
